import {ChangeEvent, DetailedHTMLProps, FC, InputHTMLAttributes, memo} from 'react'
import s from './SuperRange.module.css'

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperRangePropsType = Omit<DefaultInputPropsType, 'type'> & { //  +       
    onChangeRange?: (value: number) => void
};

export const SuperRange: FC<SuperRangePropsType> = memo((
    {
        //type, //          
        onChange, onChangeRange,
        className,

        ...restProps//       restProps
    }
) => {
    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        onChange && onChange(e) //   

        onChangeRange && onChangeRange(+e.currentTarget.value)
    }

    const finalRangeClassName = `${s.range} ${className ? className : ''}`

    return (
        <>
            <input
                type={'range'}
                onChange={onChangeCallback}
                className={finalRangeClassName}

                {...restProps} //        (value   )
            />
        </>
    )
})