import {
    DetailedHTMLProps,
    InputHTMLAttributes,
    HTMLAttributes,
    useState,
    FC, memo
} from 'react'
import s from './SuperEditableSpan.module.css'
import {SuperInputText} from '../c1-SuperInputText/SuperInputText';

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>
//    
type DefaultSpanPropsType = DetailedHTMLProps<HTMLAttributes<HTMLSpanElement>, HTMLSpanElement>

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperEditableSpanType = DefaultInputPropsType & { //  +       
    onChangeText?: (value: string) => void
    onEnter?: () => void
    error?: string
    spanClassName?: string

    spanProps?: DefaultSpanPropsType //   
}

export const SuperEditableSpan: FC<SuperEditableSpanType> = memo((
    {
        autoFocus, //    
        onBlur,
        onEnter,
        spanProps,

        ...restProps//       restProps
    }
) => {
    const [editMode, setEditMode] = useState<boolean>(false)
    const {children, onDoubleClick, className, ...restSpanProps} = spanProps || {}

    const onEnterCallback = () => {
        setEditMode(false) //  editMode   Enter

        onEnter && onEnter()
    }
    const onBlurCallback = (e: React.FocusEvent<HTMLInputElement>) => {
        setEditMode(false) //  editMode     

        onBlur && onBlur(e)
    }
    const onDoubleClickCallBack = (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => {
        setEditMode(true) //  editMode   

        onDoubleClick && onDoubleClick(e)
    }

    const spanClassName = `${s.editableSpan} ${className}`

    return (
        <>
            {editMode
                ? (
                    <SuperInputText
                        autoFocus //        true
                        onBlur={onBlurCallback}
                        onEnter={onEnterCallback}

                        {...restProps} //        (value   )
                    />
                ) : (
                    <span
                        onDoubleClick={onDoubleClickCallBack}
                        className={spanClassName}

                        {...restSpanProps}
                    >
                        {/*     ,   */}
                         {children || restProps.value}
                    </span>
                )
            }
        </>
    )
})